@use '../base/mixins' as*;

#home {
    display: grid;
    grid-template-columns: 5vw minmax(6rem, 1fr) repeat(8, minmax(min-content, 14rem) [col-end]) minmax(6rem, 1fr) 5vw;
   
    grid-template-rows:0.75em min-content min-content 70vh repeat(5, min-content) 65vw repeat(3, min-content);
    grid-row-gap: 0.75rem;
}

/*Phone Grid Media $Breakpoint*/
@include respond(phone-sm) {
    #home {
        grid-template-columns: minmax(1rem, 1fr) repeat(8,minmax(min-content, 14rem)) minmax(1rem, 1fr);

        grid-template-rows: min-content min-content 75vh repeat(5, min-content) 65vw repeat(3, min-content);
        grid-row-gap: 0.75rem;
        
        margin-left: auto;
        margin-right: auto;
      }
}


@include respond(phone-sm-plus) {
    #home {
        grid-template-columns: minmax(1rem, 1fr) repeat(8, minmax(min-content, 14rem)) minmax(1rem, 1fr);

        grid-template-rows: min-content min-content 75vh repeat(5, min-content) 65vw repeat(3, min-content);
        grid-row-gap: 0rem;
        
        margin-left: auto;
        margin-right: auto;
    }
}
@include respond(phone-md-plus) {
    #home {
        grid-template-columns: minmax(1rem, 1fr) repeat(8, minmax(min-content, 14rem)) minmax(1rem, 1fr);

        grid-template-rows: min-content min-content 75vh repeat(5, min-content) 65vw repeat(3, min-content);
        grid-row-gap: 0rem;
        
        margin-left: auto;
        margin-right: auto;
    }
}
@include respond(tab-portrait) {
    #home {
        grid-template-columns: minmax(1rem, 1fr) repeat(8, minmax(min-content, 14rem)) minmax(1rem, 1fr);

        grid-template-rows: min-content min-content 75vh repeat(5, min-content) 65vw repeat(3, min-content);
        grid-row-gap: 0rem;
        
        margin-left: auto;
        margin-right: auto;
    }
}