@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as *;
/*Top of Landing Page Header*/

.header-brand {
  position: relative;
  grid-column: 2 / -2;
  grid-row: 2 / 3;
  min-height: min-content;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr minmax(min-content,max-content) 1fr;
  justify-content: center;
  align-items: center;
  align-content: center;
  padding: 1.5rem 0;
}

.brand-container {
  position: relative;
  grid-column: 2 / -2;
  grid-row: 2 / 3;
  display: grid;
  grid-template-rows: 1.5em repeat(3, minmax(min-content,max-content)) 1.5em;
  justify-content: center;
  align-items: center;
  align-content: center;
  grid-row-gap: 1rem;
  height: 13em;
  min-width: min-content;
  margin-top: 1rem;

  .brand-logo {
    grid-row: 2 / 3;
    display: grid;
    justify-content: center;
    align-items: center;
    align-content: center;

    img {
      margin-left: auto;
      margin-right: auto;
      height: 7rem;
      width: 100%;
     }
  }
 
  .shop-title {
    grid-row: 3 / 4;
  } 
    h1 {
      font-family: f.$font-headline;
      font-size: f.$font-size-large;
      font-weight: f.$font-weight-bold;
      color: c.$color-text-black;
      letter-spacing: 5px;

      @media  only screen and (max-width: 18.75em){
        font-size: f.$font-size-normal;
      }
    }

    .shop-established {
      grid-row: 4 / 5;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 1em;

      h3 {
      font-family: f.$font-headline;
      font-weight: f.$font-weight-bold;
      font-size: f.$font-size-normal;
      color: c.$color-text-black;
      }
    }
}
/*see _header-mq.scss for media queries*/


/*HEADER NAVBAR SPANS LEFT TO RIGHT CENTRALLY*/
.header-nav {
  position: relative;
  grid-column: 2 / -2;
  grid-row: 3 / 4;
  display: grid;
  grid-template-columns: 1fr repeat(3, minmax(min-content, max-content)) 1fr;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 4rem;
  border-top: 0.1rem solid c.$color-black;
  border-bottom: 0.1rem solid c.$color-black;
}

.header-nav-list {
  position: inherit;
  grid-column: 3 / -3;
  grid-row: 3 / 4;
  display: grid;
  grid-template-columns: repeat(6, minmax(13em, 1fr));
  justify-content: center;
  align-items: center;
  align-content: center;
  grid-column-gap: 5em;
  padding: .8rem 0;
  z-index: 1000;
}


.header-nav-item-1 {
  grid-column: 1/2;
}
.header-nav-item-2 {
  grid-column: 2/3;
}
.header-nav-item-3 {
  grid-column: 3/4;
}
.header__nav__item-4 {
  grid-column: 4/5;
}
.header-nav-item-5 {
  grid-column: 5/6;
}
.header-nav-item-6 {
  grid-column: 6/7;
}

.header-nav-item {
  display: grid;
  list-style: none;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  padding: .5rem 0;
  transition: all .5s .3s;

  .header-nav-link {
    font-family: f.$font-primary;
    font-weight: f.$font-weight-bold;
    font-size: f.$font-size-normal-plus;
    color: c.$color-text-black;
  }
}

.header-nav-item:hover .header-nav-link {
  background-color: c.$color-primary-dark;
  color: #fff;
}

.header-nav-link::before,
.header-nav-link::after {
    content: "";
    width: 0;
    height: .1rem;
    background-color: c.$color-primary-dark;
    display: block;
    transition: width .3s cubic-bezier(.55, 0, .98, .54), background-color .5s .3s;
}

.header-nav-link::after {
  justify-self: end;
}

.header-nav-item:hover .header-nav-link::before,
.header-nav-item:hover .header-nav-link::after {
    width: 100%;
    background-color: #fff;
}

.second-nav-item:first-child {
  position: relative;
}

