/* Slider Header Left Start*/
.header-feature {
  position: relative;
  grid-column: 2 / -2;
  grid-row: 4 / 5;
  display: grid;
  grid-template-columns: 1fr; 
  margin-top: 1em;
}

.slider-container {
  position: inherit;
  height: 100%;
  width: 100%;
}  
.slide {
  position: absolute;
  top: 0;
  grid-column: 1 / 2;
  min-width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr repeat(8, minmax(12rem, 1fr)) 1fr;
  grid-template-rows:  repeat(9, minmax(6em, 1fr));
  justify-content: center;
  align-items: center;
  align-content: center;
  transition: opacity 1s ease-in-out;
  -webkit-transition: opacity 1s ease-in-out;
  -moz-transition: opacity 1s ease-in-out;
  -ms-transition: opacity 1s ease-in-out;
  -o-transition: opacity 1s ease-in-out;
} 

.slide-theme {
  grid-column: 2 / 5;
  grid-row: 3 / 8;
  display: grid;
  grid-template-columns: minmax(min-content, max-content);
  grid-template-rows: 1fr repeat(7,minmax(min-content, max-content)) 1fr;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: min-content;
  width: max-content;
  padding: 2rem;
  margin: auto auto auto auto;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 7px;
  z-index:  50;
}

.shop-name {
  grid-row: 2 / 3;
  display: grid;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: min-content;
  text-align: center;
  min-width: 100%;
  padding-top:  1rem;
  padding-left: 2em;
  padding-right:2em ;

  h1 {
    font-size: f.$font-size-xl *2.35;
    font-family: f.$font-script;
    color: c.$color-alt-white;
    font-weight: f.$font-weight-light;
  }
}

.theme-caption {
  grid-row: 3 / 4;
  display: grid;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: min-content;
  text-align: center;
  min-height: min-content;
  min-width: 100%;

h2 {
  font-family: f.$font-primary;
  color: c.$color-alt-white;
  font-size: f.$font-size-xl *1.075;
  letter-spacing: f.$letter-space-headline-sans;
  font-weight: f.$font-weight-normal;
  }
}

.florist-name {
  grid-row: 4 / 5;
  display: grid;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  text-align: center;
  min-height: min-content;
  min-width: 100%;
  padding-bottom: 0;
  margin-top: 1rem;
  z-index: 1000;
    
p {
  font-family: f.$font-primary;
  color: c.$color-alt-white;
  font-size: f.$font-size-normal *1.23;
  letter-spacing: f.$letter-space-headline-sans;
  font-weight: f.$font-weight-normal;
  }
}

.theme-icon {
  grid-row: 5 / 6;
  display: grid;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: min-content;
  min-width: 100%;
  padding-bottom: 0;
  margin-top: 3rem;
  z-index: 1000;

  img {
    height: 8rem;
    width: auto;
  }
}

.phone {
   position: relative;
   display: inline-flex;
   grid-row: 6 / 7;
   justify-content: center;
   align-items: center;
   align-content: center;
   text-align: center;
   min-width: min-content;
   min-height: 100%;
   margin-top: 2rem;
   margin-bottom: 0.5rem;

   .phone-icon::before {
    content: "";
    display: inline-block;
    margin-right: 1em;
    vertical-align: middle;
  }

   img {
    display: inline-block;
    height: 2rem;
    width: auto;
    margin-right: 1rem
  }

a {
    display: inline-block;
    list-style: none;
    text-decoration: none;
    color: c.$color-alt-white;
    font-family: f.$font-primary;
    font-size: f.$font-size-normal-plus *1.2;
   }
}

.email {
   position: relative;
   display: inline-flex;
   grid-row: 7 / 8;
   justify-content: center;
   align-items: center;
   align-content: center;
   text-align: center;
   min-width: min-content;
   min-height: 100%;
   margin-bottom: 2rem;

   img {
    height: 2rem;
    width: auto;
    margin-right: 1rem;
   }

  .email-icon::before {
    content: "";
    display: inline-block;
    margin-right: 1em;
    vertical-align: middle;
  }

   a {
    list-style: none;
    text-decoration: none;
    color: c.$color-alt-white;
    font-family: f.$font-primary;
    font-size: f.$font-size-normal-plus *1.2;
   }
}

.control {
  position: absolute;
  top: 0;
  width: 8vw;
  height: 100%;
  background-color: c.$color-arrow-bgGrey;
  opacity: .3;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: opacity 0.5s linear;
  z-index: 1000;
}

.arrows-visibility {
  opacity: 0;
}

.control:hover {
  opacity: 1;
}

.left-arrow {
  left: 0%;
}

.right-arrow {
  right: 0%;
}

.arrow {
  height: 7rem;
  width: auto;
}

