@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as *;

/*phone-sm-plus + (min-width:$breakpoint-phone-sm-plus) and (max-width: $breakpoint-phone-md)*/
/*START OF BREAKPOINT MIN-WIDTH (20.0625em 321px) MAX-WIDTH(26.75em 428px)*/
@include respond(phone-sm-plus) {

.header-brand {
 position: relative;
  grid-column: 1 / -1;
  grid-row: 1 / 3;
  display: grid;
  min-height: min-content;
  width: 100%;
}

.brand-container {
 grid-row: 1 / 3;
 grid-column: 1 / -1;
 display: grid;
 grid-template-columns: 1fr;
 grid-template-rows: 1fr auto 1fr;
 justify-content: center;
 align-items: center;
 align-content: center;
 min-height: min-content;
 width: 100%;
 margin: 0 0;

 .brand-logo {
   width: 100%;
   height: 100%;
   margin-left: auto;
   margin-right: auto;
   justify-self: center;
   align-self: center;
   align-content: center;

   img {
    margin-top: 1em;
    margin-left: auto;
    margin-right: auto;
    height: 7rem;
    width: 100%;
   }
 }

 .shop-title {
  grid-row: 3 / 4;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
} 
  
h1 {
    font-family: f.$font-headline;
    font-size: f.$font-size-normal *1.25;
    font-weight: f.$font-weight-bold;
    color: c.$color-text-black;
    letter-spacing: 5px;
    padding-left: auto;
    padding-right: auto;
   }
}

.hamburger {
  top: 2rem;
  right: 5vw;
}

.change .nav-content {
  width: 75vw;
}

.header-nav {
  display: none;
}

.header-feature {
  position: relative;
  grid-column: 1 / -1;
  grid-row: 3 / 4;
  display: grid;
  grid-template-columns: 1fr;
  min-width: 100%;
  height: 100%;
}

.slider-container {
  position: inherit;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.slide {
  position: absolute;
  top: 0;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-template-rows: 1fr auto 1fr;
  width: 100vw;
  height: 100%;
}

.slide-theme {
  position: relative;
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  margin: auto auto auto auto;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 7px;
  z-index:  50;
}

.shop-name h1 {
  font-size: f.$font-size-xl * 2.1;
}

.control {
 position: absolute;
 top: 0;
 width: 15vw;
 height: 100%;
 transition: opacity 0.5s linear;
 z-index: 1000;
}

.arrows-visibility {
 opacity: 0;
}

.control:hover {
 opacity: 1;
}

.left-arrow {
 left: 0%;
}

.right-arrow {
 right: 0%;
}

.arrow {
 height: 4rem;
 width: auto;
}

.slide.slide-1 {
  background: rgb(0, 0, 0);
  background-image: linear-gradient(to right, 
  rgba(0, 0, 0, 0.9) 15%, 
  rgba(0, 0, 0, 0.7) 50%, 
  rgba(0, 0, 0, 0.5) 70%, 
  rgba(0, 0, 0, 0.5) 80%, 
  rgba(0, 0, 0, 0.3) 85%, 
  rgba(0, 0, 0, 0.3) 90%, 
  rgba(0, 0, 0, 0.2) 95%, 
  rgba(0, 0, 0, 0.1) 98%, 
  rgba(0, 0, 0, 0) 100%), url("../img/background/slide-1.jpg");
  background-size: cover;
  background-position: 85%;
  object-fit: cover;
  background-repeat: no-repeat;
}
