  rgba(0, 0, 0, 0.7) 50%, 
  rgba(0, 0, 0, 0.5) 70%, 
  rgba(0, 0, 0, 0.5) 80%, 
  rgba(0, 0, 0, 0.3) 85%, 
  rgba(0, 0, 0, 0.3) 90%, 
  rgba(0, 0, 0, 0.2) 95%, 
  rgba(0, 0, 0, 0.1) 98%, 
