  background: rgb(0, 0, 0);
  background-image: linear-gradient(to right, 
  rgba(0, 0, 0, 0.9) 15%, 
  rgba(0, 0, 0, 0.7) 50%, 
  rgba(0, 0, 0, 0.5) 70%, 
  rgba(0, 0, 0, 0.5) 80%, 
  rgba(0, 0, 0, 0.3) 85%, 
  rgba(0, 0, 0, 0.3) 90%, 
  rgba(0, 0, 0, 0.2) 95%, 
  rgba(0, 0, 0, 0.1) 98%, 
  rgba(0, 0, 0, 0) 100%), url("../img/background/slide-4d.jpg");
  background-size: cover;
  background-position: 78%;
  object-fit: cover;
  background-repeat: no-repeat;
}

.slide.slide-5 {
  background: rgb(0, 0, 0);
  background-image: linear-gradient(to right, 
  rgba(0, 0, 0, 0.9) 15%, 
  rgba(0, 0, 0, 0.7) 50%, 
  rgba(0, 0, 0, 0.5) 70%, 
  rgba(0, 0, 0, 0.5) 80%, 
  rgba(0, 0, 0, 0.3) 85%, 
  rgba(0, 0, 0, 0.3) 90%, 
  rgba(0, 0, 0, 0.2) 95%, 
  rgba(0, 0, 0, 0.1) 98%, 
  rgba(0, 0, 0, 0) 100%), url("../img/background/slide-5.jpg");
  background-size: cover;
  background-position: 80%;
  object-fit: cover;
  background-repeat: no-repeat;
}


.play-pause {
 cursor: pointer;
 position: absolute;
 bottom: 5%;
 left: 50%;
 transform: translateX(-50%);
 z-index: 8000;
}

.play-pause i {
 color: #ffffff;
 font-size: 20px;
}

/*END OF BREAKPOINT MIN-WIDTH (20.0625em 321px) MAX-WIDTH(23.4375em 375px)*/
}
