    background-size: cover;
    background-position: 93%;
    object-fit: cover;
    background-repeat: no-repeat;
  }
  
  
  .play-pause {
   cursor: pointer;
   position: absolute;
   bottom: 5%;
   left: 50%;
   transform: translateX(-50%);
   z-index: 8000;
  }
  
  .play-pause i {
   color: #ffffff;
   font-size: 20px;
  }

/*END OF BREAKPOINT MIN-WIDTH (48em 768px) MAX-WIDTH(63.9375em 1023p}*/
