@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as *;

/*START OF BREAKPOINT MAX-WIDTH 20EM 320px*/
@include respond(phone-sm) {


.hamburger {
  top: 2rem;
  right: 5vw;
}

.change .nav-content {
  width: 75vw;
}
  
.header-nav {
  display: none;
}

.header-feature {
  position: relative;
  grid-column: 1 / -1;
  grid-row: 3 / 4;
  display: grid;
  grid-template-columns: 1fr;
  min-width: 100%;
  height: 100%;
}

.slider-container {
  position: inherit;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.slide {
  position: absolute;
  top: 0;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-template-rows: 1fr auto 1fr;
  width: 100vw;
  height: 100%;
}

.slide-theme {
  position: relative;
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  margin: auto auto auto auto;
  background-color: rgba(0, 0, 0, 0.099);
  border-radius: 7px;
  z-index:  50;
}


.shop-name {
  display: grid;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: min-content;
  min-width: 80%;
  padding-bottom: 0;
  margin-top: 1rem;
  margin-left: 0;
  margin-right: 0;
  margin-right: 0;

  h1 {
    font-size: f.$font-size-xl *2;
   
  }
}

.theme-caption {
  display: grid;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: min-content;
  min-width: 80%;
}

.phone {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: min-content;
  min-width: 80%;
}

.email {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: min-content;
  min-width: 80%;
}

.control {
  width: 20vw;
}

.arrow {
  height: 3.5em;
  width: auto;
}

.slide.slide-1 {
  background: rgb(0, 0, 0);
  background-image: linear-gradient(to right, 
  rgba(0, 0, 0, 0.8) 15%, 
  rgba(0, 0, 0, 0.7) 50%, 
  rgba(0, 0, 0, 0.6) 70%, 
  rgba(0, 0, 0, 0.5) 80%, 
  rgba(0, 0, 0, 0.4) 85%, 
  rgba(0, 0, 0, 0.2) 90%, 
  rgba(0, 0, 0, 0.2) 95%, 
  rgba(0, 0, 0, 0.1) 98%, 
  rgba(0, 0, 0, 0) 100%), url("../img/background/slide-1.jpg");
  background-size: cover;
  background-position: 80%;
  object-fit: cover;
  background-repeat: no-repeat;
}

