@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as m;

.intro-feature {
  margin: 18rem 0;
  grid-column: 3 / 11;
  grid-row: 5 / 6;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, min-content));
  grid-template-rows: repeat(3, minmax(min-content, max-content));
  justify-content: space-between;
}

.feature-box {
  display:grid;
  grid-template-rows: 8rem min-content;
  justify-content: center;
  align-items: end;
  align-content: center;
  row-gap: 1rem;
  height: 35rem;

  h3 {
    font-family: f.$font-headline;
    font-size: f.$font-size-xl;
    font-weight: f.$font-weight-bold;
    letter-spacing: f.$letter-space-headline-sans;
    text-align: center;
  }

  p {
    font-family: f.$font-primary;
    font-size: f.$font-size-normal *1.25;
    letter-spacing: f.$letter-space-normal;
    font-weight: f.$font-weight-normal-plus;
    text-align: center;
  }

}

.feature-image {
  position: relative;
  display: grid;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 10rem;
  width: 100%;

  img {
    position: absolute;
    top: -2rem;
    left: 30%;
    height: 13rem;
    z-index: 1000;
  }
}