@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as *;

/*phone-sm-plus + (min-width:$breakpoint-phone-sm-plus) and (max-width: $breakpoint-phone-md)*/
/*START OF BREAKPOINT MIN-WIDTH (27.0525em 433px) MAX-WIDTH(47.9em 767px)*/
@include respond(phone-md-plus) {

.hamburger {
     top: 1rem;
     right: 5vw;
   }

 .change .nav-content {
     width: 45vw;
 }
   
   .header-nav {
     display: none;
   }
   
   .header-feature {
     position: relative;
     grid-column: 1 / -1;
     grid-row: 3 / 4;
     display: grid;
     grid-template-columns: 1fr;
     min-width: 100%;
     height: 100%;
   }
   
   .slider-container {
     position: inherit;
     height: 100%;
     width: 100%;
     overflow: hidden;
   }
   
   .slide {
     position: absolute;
     top: 0;
     grid-column: 1 / 2;
     grid-row: 1 / 2;
     display: grid;
     grid-template-columns: 1fr auto 1fr;
     grid-template-rows: 1fr auto 1fr;
     width: 100vw;
     height: 100%;
   }
   
   .slide-theme {
     position: relative;
     grid-column: 2 / 3;
     grid-row: 2 / 3;
     margin: auto auto auto auto;
     background-color: rgba(0, 0, 0, 0.1);
     border-radius: 7px;
     z-index:  50;
   }
   
   .shop-name h1 {
     font-size: f.$font-size-xl * 2.1;
   }
   
   .control {
    position: absolute;
    top: 0;
    width: 15vw;
    height: 100%;
    transition: opacity 0.5s linear;
    z-index: 1000;
   }
   
   .arrows-visibility {
    opacity: 0;
   }
   
   .control:hover {
    opacity: 1;
   }
   
   .left-arrow {
    left: 0%;
   }
   
   .right-arrow {
    right: 0%;
   }
   
   .arrow {
    height: 4rem;
    width: auto;
   }
   
   .slide.slide-1 {
     background: rgb(0, 0, 0);
     background-image: linear-gradient(to right, 
     rgba(0, 0, 0, 0.9) 15%, 
     rgba(0, 0, 0, 0.7) 50%, 
     rgba(0, 0, 0, 0.5) 70%, 
     rgba(0, 0, 0, 0.5) 80%, 
     rgba(0, 0, 0, 0.3) 85%, 
     rgba(0, 0, 0, 0.3) 90%, 
     rgba(0, 0, 0, 0.2) 95%, 
     rgba(0, 0, 0, 0.1) 98%, 
     rgba(0, 0, 0, 0) 100%), url("../img/background/slide-1.jpg");
     background-size: cover;
     background-position: 85%;
     object-fit: cover;
     background-repeat: no-repeat;
   }
   .slide.slide-2 {
     background: rgba(0,0,0,0.1);
     background: rgb(0, 0, 0);
     background-image: linear-gradient(to right, 
     rgba(0, 0, 0, 0.9) 15%, 
