     background-size: cover;
     background-position: 80%;
     object-fit: cover;
     background-repeat: no-repeat;
   }
   
   
   .play-pause {
    cursor: pointer;
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 8000;
   }
   
   .play-pause i {
    color: #ffffff;
    font-size: 20px;
   }

  }
