/*
Define variables for breakpoints

$phone-breakpoint: 20em; = 320px
$tablet-portrait-breakpoint: 40em; = 640px
$tablet-landscape-breakpoint: 50em; = 800px
$laptop-breakpoint: 64em; = 1024px
$desktop-breakpoint: 80em; = 1280px

*/

/*From mixin file*/
/*Define variables for breakpoints*/
$breakpoint-phone-sm: 20em; /*320px*/
$breakpoint-phone-sm-plus: 20.0625em; /*375px*/
$breakpoint-phone-md: 26.75em; /*428px*/
$breakpoint-phone-md-plus: 26.8em; /*428.8px*/
$breakpoint-phone-lg: 27em; /*432px*/
$breakpoint-phone-lg-plus: 27.0525em; /*433px*/
$breakpoint-tablet: 47.9em; /*767px*/
$breakpoint-tablet-portrait: 48em; /*768px*/
$breakpoint-tablet-landscape: 63.9375em; /*1023px*/
$breakpoint-tablet-landscape-plus: 64em; /*1024px*/
$breakpoint-laptop: 64.7em; /*1025px*/
$breakpoint-laptop-slide: 80em; /*1280px*/
$breakpoint-desktop: 81.25em; /*1300px*/
$breakpoint-desktop-lg: 112.5em; /*1800px*/


// Use mixins to apply breakpoints
@mixin respond($breakpoint) {
  @if $breakpoint == phone-sm {
    @media only screen and (max-width: $breakpoint-phone-sm) {
      @content
    };
  }
  @if $breakpoint == phone-sm-plus {
    @media only screen and (min-width:$breakpoint-phone-sm-plus) and (max-width: $breakpoint-phone-md) {
      @content
    }
  };
@if $breakpoint == phone-md-plus {
    @media only screen and (min-width: $breakpoint-phone-lg-plus) and (max-width: $breakpoint-tablet) {
      @content
    };
  }
@if $breakpoint == tab-portrait {
    @media only screen and (min-width: $breakpoint-tablet-portrait) and (max-width: $breakpoint-tablet-landscape){
      @content
    };
  }

  @if $breakpoint == tab-landscape {
    @media only screen and (min-width: $breakpoint-tablet-landscape)  {
      @content
    };
  }
  @if $breakpoint == tab-landscape-max {
    @media only screen and (max-width: $breakpoint-tablet-landscape)  {
      @content
    };
  }

  @if $breakpoint == laptop {
    @media only screen and (min-width: $breakpoint-laptop)  {
      @content
    };
  }
  @if $breakpoint == laptop-slide-before {
    @media only screen and (max-width: $breakpoint-laptop-slide)  {
      @content
    };
  }
  @if $breakpoint == laptop-slide-after {
    @media only screen and (min-width: $breakpoint-laptop-slide)  {
      @content
    };
  }
  @if $breakpoint == desktop {
    @media only screen and (min-width: $breakpoint-desktop) {
      @content
    };
  }
  @if $breakpoint == desktop-lg {
    @media only screen and (min-width: $breakpoint-desktop-lg) {
      @content
    };
  }
}
  









